:root {
  --metallic-seaweed: #087f8c;
  --eerie-black: #212121;
  --green-sheen: #77bba9;
  --eggshell: #f1e9da;
  --lava: #d11026;
}

@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Roboto+Condensed&display=swap');

body {
  margin: 0;
  font-family: 'Roboto Condensed', sans-serif;
  background-color: var(--eggshell);
  color: var(--eerie-black);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Lato', sans-serif;
}

header {
  display: block;
  width: 100%;
  height: 30px;
  background-color: var(--lava);
}

main {
  margin: 0 24px;
}

button {
  margin-bottom: 12px;
}

button.button__toggle {
  margin-top: 18px;
}

.tile {
  border: 1px solid var(--metallic-seaweed);
  padding: 12px;
  margin-bottom: 12px;
}

.tile p {
  margin: 6px 0;
}

footer {
  background-color: var(--eerie-black);
  padding: 60px 0;
  text-align: center;
}

footer p {
  color: var(--eggshell);
}
